import React from 'react'

export default class QuoteMachine extends React.Component {
    constructor(props) {
        super(props)
        this.state = {

        }
    }

    render() {
        const quoteMachineStyles = {
            background: "white",
            textAlign: "center",
            height: "auto",
            boxShadow: "0px 0px 30px rgba(0, 0, 0, 0.4)",
            position: "absolute",
            top: "50%",
            transform: "translateY(-50%)",
            width: "100vw",
            padding: "10px 0px 30px 0px"
        }
        const quoteStyles = {
            fontStyle: "italic"
        }
        return (
            <div style={quoteMachineStyles}>
                <h1>Quote Machine</h1>
                <p style={quoteStyles}>"{this.props.quote}"</p>
                <p>{this.props.author}</p>
                <button onClick={this.props.handleQuoteButton}>New Quote</button>
            </div>
        )
    }
}
